*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content{
  height: auto;
}
.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}
/* transaction page*/
.filters{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}
/* transaction page*/

/*antd icons */
.switch-icons{
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon{
  color: black;
}
.inactive-icon{
  color: grey;
}
/*antd icons */
@media (max-width: 786px){
  .switch-icons{
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px 1px;
  }
  .anticon svg{
    font-size: 25px;
    cursor: pointer;
  }
}