@media (max-width: 768px) {
    .container{
        width: 100%;
        background-color: white;
    }
    .content{
        width: 100%;
        background-color: white;
        overflow-x: auto;
    }
    .navbar{
        width: 100%;
    }
    .container-fluid{
        width: 100%;
    }
    .filters{
        font-size: 80%;
        size: 20%;
    }
    .container-ct, .btn{
        width: 100%;
        margin-top: 10px;
        height: 120px;
        padding: 5px;
    }
    h6{
        font-size: 120%;
        font-weight: bold;
    }
    .ant-select{
        width: 100%;
    }
    .amp{
        margin-left: 3px;
    }
    .switch-icons{
        margin-left: 3px;
    }.nav-item:first-child{
        margin-left: 220px;
        position: absolute;
    }
    .nav-item:last-child{
        display: flex;
        margin-top: 30px;
    }
    
}