body {
    font-family: Arial, sans-serif;
    background-image: url('../images/theme2.jpg'); /* replace with your background image */
    background-size: cover; /* ensures the background image covers the entire viewport */
    background-position: center;
    height: 100vh;
    margin: 0;
}

.register-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.register-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.register-image {
    width: 35%;
    height: 60%;
    background-image: url('../images/theme.jpeg'); /* replace with your image */
    background-size: cover;
    background-position: center;
    opacity: 0.9;
    border-radius: 50px 0px 0px 50px;
    margin-right: 0; /* remove space between image and form */
}
.register-image > h1 {
    color: white;
}
.register-image > h3{
    color: #efefef;
    text-align: center;
    padding-top: 15%;
    font-size: 240%;
}

.register-form {
    background-color: rgba(255, 255, 255, 0.9); /* white background with 90% opacity */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    width: 300px; /* adjust width as needed */
    border: 1px solid #ccc; /* subtle border */
}

form {
    height: 60%;
    width: 30%;
    background-color: rgba(0, 0, 0, 0.7); /* light grey background color for the form */
    padding: 20px;
    border-radius: 0px 50px 50px 0px;
}

h1 {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
    color: aqua; /* changed text color to blue */
}

.register-form .ant-form-item {
    margin-bottom: 10px;
}
.ant-form-item{
    margin-left: 10%;
}
.ant-form-item-label > label {
    font-weight: bold !important;
    color: rgb(0, 255, 229) !important; /* darker text color */
}

.register-form .ant-input {
    height: 40px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.ant-input{
    width: 90%;
}

.register-form .ant-input:focus {
    border-color: #666;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.register-link{
    text-decoration: none;
    font-weight: bold;
    color: aqua;
}
.register-link:hover{
    color: rgb(6, 43, 151);
}
.zero{
    color: aquamarine;
    margin-left: 10%;
}
.btn {
    background-color: rgb(0, 166, 255);
    color: #fff;
    padding: 10px 20px;
    margin-left: 5%;
    border: none;
    border-radius: 25px;
    cursor: pointer;
}

.register-form .btn:hover {
    background-color: #3e8e41;
}

/* Spinner Styles */
.spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
}

.spinner img {
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
/* media queries*/
/* Media query for mobile devices */
@media (max-width: 768px) {
    .register-page {
        flex-direction: column;
        height: auto;
    }

    .register-container {
        flex-direction: column;
        width: 100%;
    }

    .register-image {
        margin-top: 10%;
        width: 90%;
        height: 200px;
        border-radius: 10px 10px 0px 0px;
        margin-bottom: 0px;
    }

    .register-form {
        width: 90%;
        margin: 0 auto;
        border-radius: 0px 0px 10px 10px;
    }

    form {
        width: 90%;
        border-radius: 0;
    }

    h1 {
        font-size: 24px;
    }

    .register-image > h1 {
        margin-top: 10%;
        font-size: 25px;
        margin-bottom: 0;
    }

    .register-image > h3 {
        font-size: 28px;
    }

    .register-form .ant-form-item {
        margin-left: 0;
    }

    .ant-input {
        width: 90%;
    }

    .btn {
        width: 30%;
        height: 70%;
        margin-left: auto;
    }

    .register-link {
        font-size: 16px;
    }

    span {
        font-size: 14px;
        margin-bottom: 10%;
    }
}
